.part:global(.ant-card:not(.ant-card-bordered)) {
  box-shadow: none; }

.part :global(.ant-card-head) {
  border: none;
  padding: 0;
  margin-bottom: 8px;
  min-height: auto; }

.part :global(.ant-card-body) {
  padding: 8px 0 0 0; }

.part :global(.ant-card-extra) {
  padding: 0; }

.part :global(.ant-card-head-title) {
  font-size: var(--font-size-default);
  color: var(--font-color);
  font-weight: 600;
  line-height: 20px;
  padding: 0; }
  .part :global(.ant-card-head-title):before {
    content: "";
    display: inline-block;
    width: 3px;
    height: 14px;
    background: var(--primary-color);
    border-radius: 2px;
    vertical-align: middle;
    margin-right: 8px;
    transform: translateY(-2px); }

.part .part :global(.ant-card-head) {
  padding-left: 0;
  padding-right: 0; }

.part .part :global(.ant-card-body) {
  padding: 0; }

.part .part :global(.ant-card-head-title) {
  border-radius: 12px;
  background: rgba(var(--primary-color-red), var(--primary-color-green), var(--primary-color-blue), 0.06);
  color: var(--primary-color);
  font-size: var(--font-size-small);
  line-height: 16px;
  padding: 4px 8px;
  flex: none; }
  .part .part :global(.ant-card-head-title):before {
    content: normal; }

.part .part :global(.ant-card-extra) {
  padding: 0; }
