.form:global.react-form--inner .react-form__field,
.form:global.react-form--inner--large .react-form__field,
.form:global.react-form--inner--small .react-form__field {
  margin-bottom: 16px; }

.form :global .react-form__field {
  color: var(--font-color); }
  .form :global .react-form__field input {
    color: var(--font-color); }
  .form :global .react-form__field input::placeholder {
    color: var(--font-color-grey-2); }

.form :global .react-form__field-label.is-req {
  margin-left: 0; }

.form :global .react-form__field-label {
  color: var(--font-color-grey);
  font-size: 14px;
  line-height: 20px;
  height: 20px;
  margin-bottom: 8px;
  font-weight: normal !important; }
  .form :global .react-form__field-label.is-req:before {
    color: var(--color-warning);
    content: "*";
    position: static;
    display: inline-block;
    transform: translateY(2px) scale(1.5);
    margin-right: 4px; }

.form :global .react-form__field-error {
  line-height: 14px; }

.form :global .ant-input-number,
.form :global .ant-input-number-group-wrapper {
  width: 100%; }

.form :global .ant-picker.react-form__field-component,
.form :global .data_range_picker {
  flex: 1;
  width: 100%; }

.form :global .ant-input-number-group-addon,
.form :global .ant-input-group-addon {
  background: #ffffff;
  color: var(--font-color); }

.form :global .ant-picker-suffix,
.form :global .ant-select-arrow {
  color: var(--font-color); }
  .form :global .ant-picker-suffix .anticon-down svg,
  .form :global .ant-select-arrow .anticon-down svg {
    display: none; }
  .form :global .ant-picker-suffix .anticon-down:before,
  .form :global .ant-select-arrow .anticon-down:before {
    display: block;
    font-family: "iconfont" !important;
    content: "\e693"; }
